.support-container > .support-section .support-leftnav .nav > li:before {
  width: 0;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background: #EEE;
  content: '';
  -webkit-transition: all 0.25s ease;
  -o-transition: all 0.25s ease;
  transition: all 0.25s ease;
}
.support-container > .support-section .support-leftnav .nav > li > a {
  padding: 10px 15px;
  color: #666;
}
.support-container > .support-section .support-leftnav .nav > li > a:before {
  width: 0;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background: #258FC9;
  content: '';
  -webkit-transition: all 0.25s ease;
  -o-transition: all 0.25s ease;
  transition: all 0.25s ease;
}
.support-container > .support-section .support-leftnav .nav > li > a:hover,
.support-container > .support-section .support-leftnav .nav > li > a:focus {
  background: rgba(238, 238, 238, 0.6);
}
.support-container > .support-section .support-leftnav .nav > li.active > a {
  background: #EEE;
  color: #258FC9;
}
.support-container > .support-section .support-leftnav .nav > li.active > a:before {
  width: 5px;
}
.support-container > .support-section .support-leftnav > .nav > li {
  border-top: 1px solid #EEE;
}
.support-container > .support-section .support-leftnav > .nav > li:first-child {
  border-top: none;
}
.support-container > .support-section .support-leftnav > .nav > li > .nav {
  max-height: 0;
  overflow: hidden;
  -webkit-transition: all 0.4s cubic-bezier(0, 0.6, 0, 1);
  -o-transition: all 0.4s cubic-bezier(0, 0.6, 0, 1);
  transition: all 0.4s cubic-bezier(0, 0.6, 0, 1);
}
.support-container > .support-section .support-leftnav > .nav > li > .nav > li > a {
  padding: 7.5px 15px 7.5px 30px;
  font-size: 90%;
}
