  background: #44a7dd;
}
.support-container > .support-section .support-leftnav > .nav > li.active,
.no-touch .support-container > .support-section .support-leftnav > .nav > li:hover {
  background: rgba(238, 238, 238, 0.3);
}
.support-container > .support-section .support-leftnav > .nav > li.active > .nav,
.no-touch .support-container > .support-section .support-leftnav > .nav > li:hover > .nav {
  max-height: 700px;
